<template>
<div>
  <div v-show="showAdminWithDisplay===0">
    <div id="spinner" class="spinner-border text-success" role="status">
      <span class="sr-only">Loading...</span>
    </div>
  </div>

  <div class="container-fluid">

    <div id="row_with" class="row">
      <div class="col align-self-center">
        <img src="@/assets/logo.png">
      </div>
      <div class="col align-self-center">
        <b-link id="admin_home1" to="/admin/home">Home</b-link>
      </div>
      <div class="col align-self-center">
        <b-link id="admin_deposit1" to="/admin/deposit"></b-link>
      </div>
      <div class="col align-self-center">
        <b-link id="admin_withdraw1" to="/admin/withdraw"></b-link>
      </div>
      <div class="col align-self-center">
        <b-link id="admin_balance1" to="/admin/balance"> </b-link>
      </div>
    </div>

    <table id="table_with" class="table" v-show="showAdminWithDisplay===1">
      <thead>
        <tr>
          <th colspan="2" scope="colgroup">
            Token Exchange Contract
          </th>
        </tr>
        <tr>
          <th scope="col">Token Name</th>
          <th scope="col">Token Balance</th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="key in param">
          <td>
            {{ key.tokenName }}
          </td>
          <td>
            {{key.tokenBalance}}
          </td>
        </tr>
      </tbody>

    </table>
    <div class="row" v-show="showAdminWithDisplay===1">
      <form autocomplete="off">
      <span id="with_account">  </span>
      <span id="with_tokenAmout"> , </span>
      <input id="input_with" type="text" class="form-control" v-model="toAccountAddress" placeholder="  ">
      <input id="num_amount_with" type="text" class="form-control" v-model="amountNum" placeholder="  ">
      </form>
      <select id="token_select_wtih" class="form-control" v-model="tokenSelected">
        <option value="Ether">Ether</option>
        <option value="A_token">A_token</option>
        <option value="B_token">B_token</option>
        <option value="C_token">C_token</option>
      </select>
      <button id="button_wtih" type="button" class="btn btn-primary btn-lg" v-on:click="next"></button>
    </div>

  </div>

</div>
</template>

<script>
import Web3 from 'web3';
import store from '../store';

import {
  Admin_account_address,
  TokenExchange_address,
  TokenExchange_ABI,
  ERC20_ABI,
  A_tokenAddress,
  B_tokenAddress,
  C_tokenAddress,
  Token_ABI

} from './Contract'

let tokens = [A_tokenAddress, B_tokenAddress, C_tokenAddress];

if (typeof web3 !== 'undefined') {
  // Mist/MetaMask  
  web3 = new Web3(web3.currentProvider);
  // alert('web3 .');
} else {
  //  Metamask     
  //  Metamask     
  web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:8545"));
}

let accounts;

let walletAddress;
let adminWalletAddress;

let checkToken = false;
let tokenIndex;

export default {
  data() {
    return {
      param: '',
      toAccountAddress: '',
      amountNum: '',
      tokenSelected: 'Ether'
    }
  },

  created: function() {
    store.commit('adminWithInitialization');
  },

  computed: {
    showAdminWithDisplay: function() {
     return this.$store.state.adminWithShowDisplay;
   }
 },

  methods: {
    next: function(event) {

      if (this.toAccountAddress && this.amountNum)
      {
        let checkToAccountAddress = this.toAccountAddress.substring(0, 2);

        if (checkToAccountAddress != "0x") alert("    .")
        else if (this.toAccountAddress.length != 42) {
          alert("    .")
        }
        else {

          function checkMetaMaskAccount(callback) {
            return new Promise((resolve, reject) => {
              web3.eth.getAccounts().then(res => {
                if(res)
                {
                  accounts = res;
                  resolve()
                }
                else {
                  console.error(" :       (  )");
                  alert(" :       (  )");
                  reject()
                }
              });
            });
          }

          checkMetaMaskAccount().then(()=> {

            adminWalletAddress = accounts[0];

            if(this.toAccountAddress != adminWalletAddress)
            {
              console.log("   \n          ");
              alert("   \n          ");
            }

            else if(adminWalletAddress != Admin_account_address)
            {
              console.log(adminWalletAddress);
              console.log(Admin_account_address);

              console.log('   \n       ');
              alert("   \n       ");
            }
            else {

          let TokenExchangeContract = new web3.eth.Contract(TokenExchange_ABI, TokenExchange_address)

          let inputNum = web3.utils.toWei(this.amountNum, 'ether');

          if (this.tokenSelected == "Ether") {

            if(this.amountNum > this.param[0].tokenBalance)
              alert("    \n    ");
            else {

            alert("   ");

            TokenExchangeContract.methods.withdrawEther(inputNum).send({
              from: accounts[0]
            }, (error, result) => {
              if (!error)
              {
                this.$router.push('/admin/withdraw/withdrawresult');
              }
              else
              {
                if (error.message.includes("User denied transaction signature")) {
                  alert(" .")
                } else {
                  alert("  \nSmart Contract    (  )");
                }
              }
            });
          }
          }

          if (this.tokenSelected == "A_token") {

            for(let k=0; k < this.param.length; k++)
            {
              if(this.param[k].tokenName == "A_token")
              {
                checkToken = true;
                tokenIndex = k;
              }
            }

            if(!checkToken)
              alert("    \n    ");
            else if(this.amountNum > this.param[tokenIndex].tokenBalance)
              alert("    \n    ");
            else {
            alert("   ");

            TokenExchangeContract.methods.withdrawToken(A_tokenAddress, inputNum).send({
              from: accounts[0]
            }, (error, result) => {
              if (!error)
              {
                this.$router.push('/admin/withdraw/withdrawresult');
              }
              else {
                if (error.message.includes("User denied transaction signature")) {
                  alert(" .")
                } else {
                  alert("  \nSmart Contract    (  )");
                }
              }
            });
          }
          }

          if (this.tokenSelected == "B_token") {

            for(let k=0; k < this.param.length; k++)
            {
              if(this.param[k].tokenName == "B_token")
              {
                checkToken = true;
                tokenIndex = k;
              }
            }

            if(!checkToken)
              alert("    \n    ");
            else if(this.amountNum > this.param[tokenIndex].tokenBalance)
              alert("    \n    ");
            else {

            alert("   ");

            TokenExchangeContract.methods.withdrawToken(B_tokenAddress, inputNum).send({
              from: accounts[0]
            }, (error, result) => {
              if (!error)
                this.$router.push('/admin/withdraw/withdrawresult');
              else {
                if (error.message.includes("User denied transaction signature")) {
                  alert(" .")
                } else {
                  alert("  \nSmart Contract    (  )");
                }
              }
            });
          }
          }

          if (this.tokenSelected == "C_token") {

            for(let k=0; k < this.param.length; k++)
            {
              if(this.param[k].tokenName == "C_token")
              {
                checkToken = true;
                tokenIndex = k;
              }
            }

            if(!checkToken)
              alert("    \n    ");
            else if(this.amountNum > this.param[tokenIndex].tokenBalance)
              alert("    \n    ");
            else {

            alert("   ");

            TokenExchangeContract.methods.withdrawToken(C_tokenAddress, inputNum).send({
              from: accounts[0]
            }, (error, result) => {
              if (!error)
                this.$router.push('/admin/withdraw/withdrawresult');
              else {
                if (error.message.includes("User denied transaction signature")) {
                  alert(" .")
                } else {
                  alert("  \nSmart Contract    (  )");
                }
              }
            });
          }
          }
        }
        });

        }
      }
      else
      {
        alert("  ")
      }



    },

    show: function() {

      walletAddress = TokenExchange_address;

      function getEther(callback) {
        return new Promise((resolve, reject) => {
          web3.eth.getBalance(walletAddress, (error, balance) => {
            if (!error) {
              let ether = web3.utils.fromWei(balance, 'ether'); //Push Ether : balance  to array

              store.commit('setAdminWithInfo',{
              tokenName : "ETHER",
              tokenBalance : ether
            });
              resolve()
            } else {
              console.error(" :  Ether    \n-    (web3    TokenExchange address )");
              alert(" :  Ether    \n-    (web3    TokenExchange address )");
              reject()
            }
          }); //end of getBalance
        }); //end of Promise
      } //end of getEther

      getEther()
        .then(() => { //then search ERC20 tokens

          const getTokenBalance = new Promise((resolve, reject) => {
            let ctn = 0;

            tokens.forEach((token) => {
              let contract = new web3.eth.Contract(ERC20_ABI, token);

              contract.methods.balanceOf(walletAddress).call({
                from: Admin_account_address
              }, (error, balance) => {
                contract.methods.decimals().call({
                  from: Admin_account_address
                }, (error, decimals) => {
                  if (!error) {

                    let calculatedResult = balance / (Math.pow(10, decimals));

                    contract.methods.name().call({
                      from: Admin_account_address
                    }, (error, tokenName) => {

                      ctn++;

                      if (balance.toString() != '0') {
                        store.commit('setAdminWithInfo',{
                        tokenName : tokenName,
                        tokenBalance : calculatedResult
                      });
                      }
                      if (ctn == tokens.length) {
                        ctn = 0;
                        resolve()
                      }
                    }); //end of name
                  } else {
                    console.error(" :  Token    \n-    ( Token address )");
                    alert(" :  Token    \n-    ( Token address )");
                    console.log(error);
                  }
                }); //end of decimals
              }); //end of balanceof
            }); //end of tokens

          });

          getTokenBalance.then(result => {
            this.param = store.state.adminWithInfo;
            store.commit('changeAdminWithDisplay', 1);
          });

        });

    }
  },

  mounted() {
    this.show();
  }

}
</script>

<style>
th {
  background-color: #29B6F6;
  color: #FFFFFF;
  text-align: center;
}

td {
  text-align: center;
}

#row_with {
  /* border: 2px solid; */
  padding: 10px;
  width: 100%;
  resize: both;
  overflow: auto;
  left: 0;
  right: 0;
  position: absolute;
}

#admin_home1,
#admin_deposit1,
#admin_withdraw1,
#admin_balance1 {
  font-size: 30px;
  color: green;
}

#table_with {
  width: 40%;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#with_account {
  position: absolute;
  top: 65%;
  left: 35%;
  transform: translate(-50%, -50%);
  font-weight: 800;
}

#with_tokenAmout {
  position: absolute;
  top: 75%;
  left: 35%;
  transform: translate(-50%, -50%);
  font-weight: 800;
}

#input_with {
  position: absolute;
  top: 65%;
  left: 55%;
  transform: translate(-50%, -50%);
  width: 25%;
}

#num_amount_with {
  width: 15%;
  position: absolute;
  top: 75%;
  left: 60%;
  transform: translate(-50%, -50%);
}

#token_amount {
  float: left;
  margin-left: -900px;
  margin-top: 300px;
  width: 170px;
  height: 40px;
  font-size: 20px;
  font-family: sans-serif;
}

#token_select_wtih {
  width: 8%;
  height: 4%;
  position: absolute;
  top: 75%;
  left: 47%;
  transform: translate(-50%, -50%);
}

#button_wtih {
  width: 8%;
  height: 6%;
  position: absolute;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#spinner {
  position: fixed;
  top: 50%;
  left: 50%;
}
</style>



